@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color: #e0e9f5;
}

main {
  margin-top: 7rem;
  text-align: center;
}

h1,
h2,
h3,
p {
  color: #042b5f;
}

.MainHeader_header__3jG-X {
  width: 100%;
  height: 5rem;
  background-color: #044599;
  padding: 0 10%;
}

.MainHeader_header__3jG-X nav {
  height: 100%;
}

.MainHeader_header__3jG-X ul {
  height: 100%;
  list-style: none;
  display: flex;
  padding: 0;
  margin: 0;
  align-items: center;
  justify-content: center;
}

.MainHeader_header__3jG-X li {
  text-align: center;
  margin: 0 1rem;
  width: 5rem;
}

.MainHeader_header__3jG-X a {
  color: white;
  text-decoration: none;
}

.MainHeader_header__3jG-X a:hover,
.MainHeader_header__3jG-X a:active,
.MainHeader_header__3jG-X a.MainHeader_active__11wpQ {
  color: #95bcf0;
  padding-bottom: 0.25rem;
  border-bottom: 4px solid #95bcf0;
}

